import axios from 'axios'
import { Fragment } from 'react'
const Baseurl = 'http://localhost:3997/api/'
// const Baseurl = 'https://adworks.app/api/'
import { toast, Slide } from 'react-toastify'
import { getProducts } from '@src/views/apps/ecommerce/store/actions'

const ToastContent = ({ title, text }) => (
    <Fragment>
        <div className='toastify-header'>
            <div className='title-wrapper'>
                <h6 className='toast-title font-weight-bold'>Success, {title}</h6>
            </div>
        </div>
        <div className='toastify-body'>
            <span>{text ? text : 'You have successfully added your task in database!'}</span>
        </div>
    </Fragment>
)


// ** Get Tasks
export const getTasks = params => {
    return dispatch => {
        return axios.post(`${Baseurl}/jobs/getTasks`, params).then(res => {
            dispatch({
                type: 'GET_TASKS',
                tasks: res.data.data,
                params
            })
        })
    }
}
export const getTasksC = params => {
    return dispatch => {
        return axios.post(`${Baseurl}/jobs/getTasks`, params).then(res => {
            dispatch(getProducts(res.data.data, params, res.data.totalNum))
        })
    }
}

export const setStoreStage = (stage) => {
    return dispatch => {
        dispatch({
            type: 'SET_STAGE',
            stage
        })
    }
}

// ** Re-order Tasks on drag
export const reOrderTasks = tasks => dispatch => dispatch({ type: 'REORDER_TASKS', tasks })

// ** ADD Task
export const addTask = task => {
    return (dispatch, getState) => {
        axios
            .post(`${Baseurl}/jobs/addTask`, { task })
            .then(res => {
                dispatch({
                    type: 'ADD_TASK',
                    task: res.data
                })
                toast.success(
                    <ToastContent title={res.data.title} />,
                    { transition: Slide, hideProgressBar: true, autoClose: 2000 }
                )
            })
            .then(dispatch(getTasks(getState().todo.params)))
    }
}

// ** Update Tasks
export const updateTask = task => {
    return (dispatch, getState) => {
        axios
            .post(`${Baseurl}/jobs/updateTask`, { task })
            .then(res => {
                dispatch({
                    type: 'UPDATE_TASK',
                    task: res.data
                })
                toast.success(
                    <ToastContent title={res.data.title} text={'You have successfully updated your task in database!'} />,
                    { transition: Slide, hideProgressBar: true, autoClose: 2000 }
                )
            })
            .then(dispatch(getTasks(getState().todo.params)))
    }
}

// ** Delete Task
export const deleteTask = taskId => {
    return (dispatch, getState) => {
        axios
            .post(`${Baseurl}/jobs/deleteTask`, { taskId })
            .then(res => {
                dispatch({
                    type: 'DELETE_TASK',
                    task: res.data
                })
                toast.success(
                    <ToastContent title={res.data.title} text={'You have successfully deleted your task in database!'} />,
                    { transition: Slide, hideProgressBar: true, autoClose: 2000 }
                )
            })
            .then(() => dispatch(getTasks(getState().todo.params)))
    }
}

// ** Select Task
export const selectTask = task => dispatch => dispatch({ type: 'SELECT_TASK', task })


export const getPackages = () => {
    return dispatch => {
        return axios.get(`${Baseurl}/jobs/getPackages`).then(res => {
            dispatch({
                type: 'GET_PACAKGES',
                packages: res.data.packages
            })
        })
    }
}
export const makePackage = dataArr => {
    return (dispatch, getState) => {
        axios.post(`${Baseurl}/jobs/makePackage`, { dataArr }).then(res => {
            dispatch(getPackages(getState().todo.params))

            toast.success(
                <ToastContent title={res.data.title} text={'You have successfully deleted your task in database!'} />,
                { transition: Slide, hideProgressBar: true, autoClose: 2000 }
            )
        })
    }
}

export const deletePackage = (id) => {
    return (dispatch, getState) => {
        return axios.post(`${Baseurl}/jobs/deletePackages`, { id }).then(res => {
            dispatch(getPackages(getState().todo.params))
            toast.success(
                <ToastContent title={res.data.title} text={'You have successfully deleted your package in database!'} />,
                { transition: Slide, hideProgressBar: true, autoClose: 2000 }
            )

        })
    }
}
