// ** React Imports
import { Link } from 'react-router-dom'

// ** Third Party Components
import classnames from 'classnames'
import { Star, ShoppingCart, Heart } from 'react-feather'
import { Card, CardBody, CardText, Button, Badge } from 'reactstrap'
import { useSelector } from 'react-redux'


const ProductCards = props => {
  // ** Props
  const {
    store,
    products,
    activeView,
    addToCart,
    dispatch,
    getProducts,
    getCartItems,
    addToWishlist,
    deleteWishlistItem
  } = props

  // ** Handle Move/Add to cart
  const cart = useSelector(state => state.ecommerce.cart)
  const handleCartBtn = (item, val) => {
    if (val === false) {
      const taskTemp = []
      let cartTemp = []
      for (let i = 0; i < store.tasks.length; i++) {
        if (item._id === store.tasks[i]._id) {
          taskTemp.push(store.tasks[i])
          taskTemp[i].isInCart = true
        } else {
          taskTemp.push(store.tasks[i])
        }
      }
      cartTemp = cart
      cartTemp.push(item)
      dispatch(addToCart(taskTemp, cartTemp))
    }
  }

  // ** Renders products
  const renderProducts = () => {
    if (products.length) {
      return products.map((item, index) => {
        const CartBtnTag = item.isInCart ? Link : 'button'

        return (
          <Card className='ecommerce-card' key={index}>
            <CardBody>
              <h6 className='item-name'>
                <Link className='text-body' to={`#`}>
                  {`Task-Id : ${index + 1000}`}
                </Link>
              </h6>
              <h4 className='text-primary'>{item.title}</h4>
              <CardText className='text-success'>{`Price: ${item.tokenType} ${item.budget}`}</CardText>
              <CardText className='text-info'>{`User Number : ${item.userNumber}`}</CardText>
            </CardBody>
            <div className='item-options text-center'>
              <div className='item-wrapper'>
                <div className='item-cost'>
                  <h4 className='item-price'>{`${item.tokenType} ${item.budget}`}</h4>
                </div>
              </div>
              <Button
                className='btn-cart move-cart'
                color='ddd'
                tag={Link}
                disabled
                to={`/apps/task/task-detail/${item.slug}`}
              >
              </Button>
              <Button
                color='primary'
                tag={CartBtnTag}
                className='btn-cart move-cart'
                onClick={() => handleCartBtn(item, item.isInCart)}
                /*eslint-disable */
                {...(item.isInCart
                  ? {
                    to: '#'
                  }
                  : {})}
              /*eslint-enable */
              >
                <ShoppingCart className='mr-30' size={14} />
                <span>{item.isInCart ? 'Added In Cart' : 'Add To Cart'}</span>
              </Button>
            </div>
          </Card >
        )
      })
    }
  }

  return (
    <div
      className={classnames({
        'grid-view': activeView === 'grid',
        'list-view': activeView === 'list'
      })}
    >
      {renderProducts()}
    </div>
  )
}

export default ProductCards
